import { useLanguage } from '../stores/language';
import { useResources, useStaticResources } from '../stores/resources';

import { translate } from '../utils';

import { fallbackLanguage } from '../constants';

export type TranslateFn = (key: string, ...params: string[]) => string;

export const useTranslation = (): TranslateFn => {
  const staticResources = useStaticResources();
  const resources = useResources();
  const language = useLanguage();

  return (key: string, ...params: string[]) =>
    translate(
      language,
      fallbackLanguage,
      resources,
      key,
      staticResources,
      ...params,
    );
};
