import styled, { css } from 'styled-components';

const setBorder = (color: string) => css`
  background-color: ${color};

  padding: 0;
  position: absolute;
  top: 0;
  content: '';
  width: 1px;
  height: 100%;
`;

export const RadioGroupWrapper = styled.div`
  .ant-radio-group.ant-radio-group-solid {
    display: flex;
    width: 100%;

    @media (max-width: 600px) {
      width: 100%;

      overflow-x: scroll;
      overflow-y: hidden;

      &::-webkit-scrollbar {
        display: none;
      }
    }

    .ant-radio-button-wrapper {
      ${({ theme }) => css`
        background-color: ${theme.colors.petrol80};
        color: ${theme.colors.white};

        border-radius: 0;
        border: none;

        padding: 9px 15px;
        line-height: 15px;

        span {
          white-space: nowrap;
        }

        &:not(:first-child) {
          margin-left: 1px;

          &::before {
            ${setBorder(theme.colors.petrol)};

            left: -1px;
          }
        }
      `}
    }

    .ant-radio-button-wrapper-checked {
      ${({ theme }) => css`
        background-color: ${theme.colors.petrol40K};

        &::after {
          ${setBorder(theme.colors.petrol40K)};

          right: -1px;
        }
      `}
    }

    .ant-radio-button-wrapper-checked:not(.ant-radio-button-wrapper-disabled)::before {
      background-color: ${({ theme }) => theme.colors.petrol40K};
    }
  }
`;
