import React, { useCallback } from 'react';
import { useNavigate } from 'react-router-dom';

import { Header } from '@daimler/react-ux-business-app';

import { Path } from '@router/types';

import { AppHeaderWrapper, HomeIconWrapper } from './styled';

import { useCurrentMandant } from '@mandants/index';
import { useTranslation } from '@daimler/react-ux-i10n';

import { RightSection } from './components/RightSection';
import { ReactComponent as HomeIcon } from '@icons/home.svg';

export const AppHeader: React.FC = () => {
  const navigate = useNavigate();
  const handleBack = useCallback(
    () =>
      window.location.pathname == Path.DASHBOARD
        ? navigate(Path.MANDANTS)
        : navigate(-1),
    [],
  );
  const handleHome = useCallback(() => navigate(Path.DASHBOARD), []);

  const mandant = useCurrentMandant();
  const t = useTranslation();
  const title = `${t('Rabs.Application.Title')} ${
    mandant ? ` | ${mandant.name}` : ``
  }`;

  return (
    <AppHeaderWrapper>
      <Header
        onBack={handleBack}
        title={title}
        leftSection={
          <HomeIconWrapper>
            <HomeIcon onClick={handleHome} />
          </HomeIconWrapper>
        }
        rightSection={<RightSection />}
      />
    </AppHeaderWrapper>
  );
};
