import React, { memo } from 'react';

import {
  TilesWrapper,
  TilesHead,
  TilesBody,
  TilesTitle,
  TilesDescription,
  IconWrapper,
} from './styled';

export type TilesProps = {
  title: string;
  description?: string;
  icon?: React.ReactNode;
  section?: React.ReactNode;
};

const TilesComponent: React.FC<TilesProps> = ({
  title,
  description,
  icon,
  section,
}) => {
  return (
    <TilesWrapper>
      <TilesHead>
        <TilesTitle>{title}</TilesTitle>
        {description && <TilesDescription>{description}</TilesDescription>}
      </TilesHead>
      <TilesBody>
        <IconWrapper>{icon}</IconWrapper>

        {section}
      </TilesBody>
    </TilesWrapper>
  );
};

export const Tiles = memo(TilesComponent);
