import React, { memo, forwardRef, PropsWithChildren } from 'react';

import { Drawer } from 'antd';

import { MobileModalWrapper } from './styled';

type Ref = HTMLDivElement;
type MobileSheetProps = PropsWithChildren<{
  title: React.ReactNode;
  open: boolean;
  onCancel: () => void;
  height?: number;
}>;

const MobileSheetComponent = forwardRef<Ref, MobileSheetProps>((props, ref) => (
  <MobileModalWrapper ref={ref}>
    <Drawer
      height={props.height}
      title={props.title}
      placement="bottom"
      closable={false}
      open={props.open}
      onClose={props.onCancel}
    >
      {props.children}
    </Drawer>
  </MobileModalWrapper>
));

export const MobileSheet = memo(MobileSheetComponent);

MobileSheet.displayName = 'AppLoader';

export type { MobileSheetProps };
