import React, { memo, forwardRef } from 'react';

import { SelectWrapper, StyledSelect } from '../MultiSelect/styled';
import type { Option } from '../MultiSelect/types';

import { Variant } from '../MultiSelect/constants';

import { ChervoneDownIcon } from '../../icons';

import { theme } from '../../theme/theme';

export type SelectProps = {
  options: Option[];
  onSelect: (value: string) => void;
  value?: string;
  placeholder: string;
  variant?: Variant;
};

const SelectComponent = forwardRef<HTMLDivElement, SelectProps>(
  (
    { options, onSelect, value, placeholder, variant = Variant.Default },
    ref,
  ) => (
    <SelectWrapper variant={variant} ref={ref}>
      <StyledSelect
        value={value}
        placeholder={placeholder}
        onChange={(value) => onSelect(value as string)}
        suffixIcon={<ChervoneDownIcon fill={theme.colors.petrol} />}
      >
        {options.map(({ label, value }) => (
          <StyledSelect.Option key={value} value={value}>
            {label}
          </StyledSelect.Option>
        ))}
      </StyledSelect>
    </SelectWrapper>
  ),
);

export const Select = memo(SelectComponent);
