import React from 'react';
import { theme } from '../../../../theme/theme';

import { CheckIcon } from '../../../../icons';

import { StyledSelectAll } from './styled';

type SelectAllProps = {
  onSelectAll: () => void;
  selectAllPlaceholder: string;
  children: React.ReactNode;
  isAllSelected: boolean;
};

export const SelectAll: React.FC<SelectAllProps> = ({
  onSelectAll,
  selectAllPlaceholder,
  isAllSelected,
  children,
}) => (
  <>
    <StyledSelectAll onClick={onSelectAll}>
      {selectAllPlaceholder}
      {isAllSelected ? <CheckIcon fill={theme.colors.petrol} /> : <div />}
    </StyledSelectAll>
    {children}
  </>
);
