import { BrowserRouter } from 'react-router-dom';
import type { BrowserRouterProps } from 'react-router-dom';
import React from 'react';
import { baseName } from '@api/client/constants';

export const RouterProvider: React.FC<BrowserRouterProps> = (props) => {
  const hasExtra = baseName?.length > 1 && baseName.slice(-1) === '/';
  const newBaseName = hasExtra ? baseName.slice(0, -1) : baseName;
  return <BrowserRouter {...props} basename={newBaseName} />;
};
