import { useCallback, useState } from 'react';

type useToggleType = (
  params?: Partial<{
    defaultValue: boolean;
    onOpen: () => void;
    onClose: () => void;
    onToggle: () => void;
  }>,
) => {
  open: boolean;
  handleOpen: () => void;
  handleClose: () => void;
  handleToggle: () => void;
};

export const useToggle: useToggleType = ({
  defaultValue,
  onOpen,
  onClose,
  onToggle,
} = {}) => {
  const [open, setOpen] = useState(defaultValue ?? false);

  const handleOpen = useCallback(() => {
    setOpen(true);
    onOpen?.();
  }, []);

  const handleClose = useCallback(() => {
    setOpen(false);
    onClose?.();
  }, []);

  const handleToggle = useCallback(() => {
    setOpen((open) => !open);
    onToggle?.();
  }, []);

  return {
    open,
    handleOpen,
    handleClose,
    handleToggle,
  };
};
