import React from 'react';
import { Trans } from '@i10n/index';

import { notification } from '@daimler/react-ux-business-app';

import { webAdminBase } from './constants';

export const loginUrl = () =>
  `/auth/login?returnUrl=${encodeURIComponent(window.location.href)}`;

export const openErrorNotification = () => {
  notification.error({
    key: 'error',
    message: <Trans value="Rabs.AppErrors.AsyncOperationError.Message" />,
  });
};

export const getUrl = (route: string) =>
  `${webAdminBase}${route.startsWith('/') ? route.slice(1) : route}`;
