import { createEvent, restore } from 'effector';
import { useStore } from 'effector-react';

import { LocalLanguage } from '../constants';

export const setLanguages = createEvent<string[]>('SET_LANGUAGE');
export const $languages = restore(setLanguages, []);

export const useLanguages = () => useStore($languages);

export const setLanguage = createEvent<LocalLanguage>('SET_LANGUAGE');
export const $language = restore<LocalLanguage>(setLanguage, LocalLanguage.DE);

export const useLanguage = () => useStore($language);
