import React, { forwardRef, memo } from 'react';

import { BaseButton, ButtonSize, SizeType } from '../base';
import type { BaseButtonProps, ButtonRef } from '../base';

import { RoundedButtonWrapper } from './styled';

type RoundedButtonProps = { size?: ButtonSize } & BaseButtonProps;

const RoundedButtonComponent = forwardRef<ButtonRef, RoundedButtonProps>(
  ({ size = ButtonSize.medium, disabled, ...restProps }, ref) => (
    <RoundedButtonWrapper disabled={!!disabled}>
      <BaseButton
        {...restProps}
        shape="round"
        ref={ref}
        size={size as SizeType}
        disabled={disabled}
      />
    </RoundedButtonWrapper>
  ),
);

export const RoundedButton = memo(RoundedButtonComponent);

RoundedButton.displayName = 'RoundedButton';

export { ButtonSize };
export type { RoundedButtonProps };
