import React, { forwardRef, memo } from 'react';

import { BaseButton, ButtonSize } from '../base';
import type { BaseButtonProps, ButtonRef, SizeType } from '../base';

import { PrimaryButtonWrapper } from './styled';

type PrimaryButtonProps = { size?: ButtonSize } & BaseButtonProps;

const PrimaryButtonComponent = forwardRef<ButtonRef, PrimaryButtonProps>(
  ({ size = ButtonSize.medium, disabled, ...restProps }, ref) => (
    <PrimaryButtonWrapper disabled={!!disabled}>
      <BaseButton
        {...restProps}
        type="primary"
        ref={ref}
        size={size as SizeType}
      />
    </PrimaryButtonWrapper>
  ),
);

export const PrimaryButton = memo(PrimaryButtonComponent);

PrimaryButton.displayName = 'PrimaryButton';

export { ButtonSize };
export type { PrimaryButtonProps };
