import React, { useCallback } from 'react';

import { Dropdown } from '@daimler/react-ux-business-app';

import { useLanguage, useLanguages, setLanguage } from '@daimler/react-ux-i10n';
import type { LocalLanguage } from '@daimler/react-ux-i10n';

export const LocalizationDropdown = () => {
  const language = useLanguage();
  const languages = useLanguages();

  const handleSelect = useCallback((language: string) => {
    setLanguage(language as LocalLanguage);
  }, []);

  return (
    <Dropdown value={language} options={languages} onSelect={handleSelect} />
  );
};
