import React from 'react';
import { Radio as AntdRadio } from 'antd';
import type { RadioGroupProps, RadioChangeEvent } from 'antd';
import type { RadioButtonProps } from 'antd/lib/radio/radioButton';

import { RadioGroupWrapper } from './styled';

export const RadioGroup: React.FC<RadioGroupProps> & {
  Button: React.FC<RadioButtonProps>;
} = (props) => (
  <RadioGroupWrapper>
    <AntdRadio.Group {...props} buttonStyle="solid" />
  </RadioGroupWrapper>
);

RadioGroup.Button = AntdRadio.Button;

RadioGroup.displayName = 'RadioGroup';

export type { RadioChangeEvent, RadioGroupProps };
