import React, { lazy } from 'react';

import { Path, RoutesType, TileGroup } from '@router/types';

import { Trans } from '@i10n/index';

import { Permission } from '@permissions/constants';

const DashboardPage = lazy(() => import('./dashboard'));
const InvoicesPage = lazy(() => import('./invoices'));
const InvoicePage = lazy(() => import('./invoice'));
const MandantsPage = lazy(() => import('./mandants'));
const GoodsMovementsPage = lazy(() => import('./goods-movements'));
const GoodsMovementPage = lazy(() => import('./goods-movement'));

export const routes: RoutesType = [
  {
    path: Path.MANDANTS,
    element: <MandantsPage />,
    title: <Trans value="Rabs.Mandants.PageTitle" />,
    description: null,
    tileGroup: undefined,
    permissions: undefined,
  },
  {
    path: Path.DASHBOARD,
    element: <DashboardPage />,
    title: '',
    description: null,
    tileGroup: undefined,
    permissions: undefined,
  },
  {
    path: Path.INVOICES,
    element: <InvoicesPage />,
    title: <Trans value="Rabs.Invoices.PageTitle" />,
    description: <Trans value="Rabs.Invoices.Tile.Description" />,
    tileGroup: TileGroup.INVOICES,
    permissions: [Permission.WEB_CLIENT_MANAGER],
  },
  {
    path: Path.INVOICE,
    element: <InvoicePage />,
    title: <Trans value="Rabs.Invoice.PageTitle" />,
    description: null,
    tileGroup: undefined,
    permissions: [Permission.WEB_CLIENT_MANAGER],
  },
  {
    path: Path.INVOICEWITHFILTER,
    element: <InvoicePage />,
    title: <Trans value="Rabs.Invoice.PageTitle" />,
    description: null,
    tileGroup: undefined,
    permissions: [Permission.WEB_CLIENT_MANAGER],
  },
  {
    path: Path.GOODSMOVEMENTS,
    element: <GoodsMovementsPage />,
    title: <Trans value="Rabs.GoodsMovements.PageTitle" />,
    description: <Trans value="Rabs.GoodsMovements.Tile.Description" />,
    tileGroup: TileGroup.INVOICES,
    permissions: [Permission.WEB_CLIENT_MANAGER],
  },
  {
    path: Path.GOODSMOVEMENT,
    element: <GoodsMovementPage />,
    title: <Trans value="Rabs.GoodMovement.PageTitle" />,
    description: null,
    tileGroup: undefined,
    permissions: [Permission.WEB_CLIENT_MANAGER],
  },
  {
    path: Path.GOODSMOVEMENTWITHFILTER,
    element: <GoodsMovementPage />,
    title: <Trans value="Rabs.GoodMovement.PageTitle" />,
    description: null,
    tileGroup: undefined,
    permissions: [Permission.WEB_CLIENT_MANAGER],
  },
];
