import styled, { css } from 'styled-components';

const setBorder = (color: string) => css`
  background-color: ${color};

  position: absolute;
  width: 100%;
  left: 0px;
  content: '';
  height: 2px;
`;

export const TabsWrapper = styled.div`
  .ant-tabs-nav {
    padding: 11px 0 0 10px;

    margin-bottom: 0;
  }

  .ant-tabs-nav::before {
    border-color: ${({ theme }) => theme.colors.coolGrey20K};
  }

  .ant-tabs-tab {
    ${({ theme }) => css`
      background-color: ${theme.colors.coolGrey} !important;
      border-color: ${theme.colors.coolGrey20K} !important;
    `}

    transition: none !important;
    border-radius: 0 !important;
    margin-left: 0 !important;

    &:not(.ant-tabs-tab-active) {
      color: ${({ theme }) => theme.colors.petrol};
    }

    &:not(:first-child) {
      border-left: none;
    }

    &.ant-tabs-tab-active {
      ${({ theme }) => css`
        background-color: ${theme.colors.white} !important;

        .ant-tabs-tab-btn {
          color: ${theme.colors.petrol};
        }

        &::after {
          ${setBorder(theme.colors.petrol)};

          top: -1px;
        }

        &::before {
          ${setBorder(theme.colors.white)};

          bottom: -1px;
        }
      `}
    }
  }
`;
