import { ApiClientBase as ApiClient } from '../generated/apiClient.generated';

import { HttpStatusCode, createCustomFetch } from '@daimler/react-ux-web-api';

import { baseUrl } from './constants';

import { loginUrl } from './utils';
import { localStorageMandantIdKey } from '@mandants/constants';

const customFetch = createCustomFetch({
  beforeRequest: (init?: RequestInit) => {
    const mandantId = localStorage.getItem(localStorageMandantIdKey) ?? '';

    const headers = new Headers({
      ...init?.headers,
      'X-Mandant': mandantId,
    });

    return {
      ...init,
      headers,
    };
  },
  afterRequest: async (resp) => {
    if (resp.status === HttpStatusCode.UNAUTHORIZED) {
      window.location.href = loginUrl();
    }

    if (resp.redirected && resp.status === HttpStatusCode.OK) {
      const text = await resp.text();

      if (text.includes('login')) {
        window.location.href = loginUrl();
      }
    }

    return resp;
  },
});

export const apiClient = new ApiClient(baseUrl, { fetch: customFetch });
