import { objToQuery, customFetch } from './utils';

import type {
  Language,
  ResourceParams,
  ResourcesThin,
  TextResourcesProvider,
} from '../types';

export function createDynamicResourceProvider(
  url: string,
): TextResourcesProvider {
  async function getLanguages(): Promise<Language[] | null> {
    return customFetch(`${url}/languages`);
  }

  async function getResources(
    params: ResourceParams | undefined,
  ): Promise<ResourcesThin | null> {
    if (!params) {
      return customFetch(`${url}/resources`);
    }

    const queryParams = objToQuery(params);

    return customFetch(`${url}/resources${queryParams}`);
  }

  return {
    getLanguages,
    getResources,
  };
}
