export const baseName = document.querySelector('base')
  ? new URL(document.head.baseURI).pathname
  : '/';

const internalApiBase = 'web-client/';

export const webAdminBase = baseName.endsWith(internalApiBase)
  ? baseName.slice(0, baseName.length - internalApiBase.length)
  : baseName;

export const baseUrl = `${webAdminBase}api/web-client`;
