import {
  defaultApiType,
  defaultApiName,
  defaultModuleName,
  fetchOptions,
} from './constants';

import type { HostModuleConfig } from './types';

import { LocalLanguage, OK } from './constants';

import type { ResourcesThin } from './providers/types';
import type { GetLocalizationEndpointParams } from './types';

export const flattenObject = (object: any) => {
  const flatObject: any = {};

  Object.keys(object).forEach((property) => {
    if (typeof object[property] === 'object') {
      const flatSubobject = flattenObject(object[property]);

      Object.keys(flatSubobject).forEach((subproperty) => {
        flatObject[`${property}.${subproperty}`] = flatSubobject[subproperty];
      });
    } else {
      flatObject[property] = object[property];
    }
  });

  return flatObject;
};

export const flattenResources = (resourcesData: ResourcesThin) => {
  Object.keys(resourcesData).forEach((lang) => {
    resourcesData[lang] = flattenObject(resourcesData[lang]);
  });

  return resourcesData;
};

const getStaticResource = (languageTags: Object, key: string) => {
  const keys = key.split('.');

  return keys.reduce(
    // @ts-ignore: remove it after imvestigation
    (prev, curr: string) => prev?.[curr],
    languageTags,
  ) as string;
};

const findTranslation = (
  resources: ResourcesThin,
  key: string,
  lang: string,
  isStatic = false,
): string | undefined => {
  if (!resources) {
    return undefined;
  }

  const languageTags = resources[lang];
  if (!languageTags) {
    return undefined;
  }

  const value = isStatic
    ? getStaticResource(languageTags, key)
    : languageTags && languageTags[key];

  return value ?? undefined;
};

const replacePositionalParams = (input: string, args: string[]): string => {
  let result = input;

  args.forEach((arg, i) => {
    result = result.replace(`{${i}}`, arg);
  });

  return result;
};

export const translate = (
  language: LocalLanguage,
  fallbackLanguage: LocalLanguage | undefined,
  resources: ResourcesThin,
  key: string,
  staticResources: ResourcesThin,
  ...params: string[]
): string => {
  if (!key) return '';

  let translation = findTranslation(resources, key, language);
  if (!translation && fallbackLanguage && fallbackLanguage !== language) {
    translation = findTranslation(resources, key, fallbackLanguage);
  }

  // if not found it dynamic -> check static
  if (!translation) {
    translation = findTranslation(staticResources, key, language, true);
  }

  if (translation) {
    if (params) {
      return replacePositionalParams(translation, params);
    }

    return translation;
  }

  let paramsString = '';
  if (params && params.length > 0) {
    paramsString = `,${params.join(',')}`;
  }

  return `?${language}:${key}${paramsString}?`;
};

export async function getLocalizationEndpoint(
  params: GetLocalizationEndpointParams,
): Promise<string> {
  const {
    url,
    moduleName = defaultModuleName,
    apiName = defaultApiName,
    apiType = defaultApiType,
  } = params;

  try {
    const res = await fetch(url, fetchOptions);

    if (res.status !== OK) return '';

    const text = await res.text();

    if (text === '' || text.startsWith('<')) return '';

    const hostModuleConfig: HostModuleConfig = JSON.parse(text);

    const moduleInfo = hostModuleConfig.modules.find(
      (module) => module.name === moduleName,
    );

    if (!moduleInfo) {
      throw new Error(`module is ${moduleName} not found`);
    }

    const endpoint = moduleInfo.endpoints.find(
      (endpoint) => endpoint.type === apiType && endpoint.name === apiName,
    );

    if (!endpoint) {
      throw new Error(`module ${moduleName} does not have ${apiType} endpoint`);
    }

    return endpoint.url;
  } catch (error) {
    console.error(error);

    return '';
  }
}
