import { createEvent, restore } from 'effector';
import { useStore } from 'effector-react';

import type { ResourcesThin } from '../providers/types';

export const setStaticResources = createEvent<ResourcesThin>(
  'SET_STATIC_RESOURCES',
);
export const $staticResources = restore(setStaticResources, {});

export const useStaticResources = () => useStore($staticResources);

export const setResources = createEvent<ResourcesThin>('SET_RESOURCES');
export const $resources = restore<ResourcesThin>(setResources, {});

export const useResources = () => useStore($resources);
