import type {
  Language,
  ResourceParams,
  ResourcesThin,
  TextResourcesProvider,
} from '../types';

export function createStaticResourceProvider(
  staticResources: ResourcesThin,
): TextResourcesProvider {
  async function getLanguages() {
    return Object.keys(staticResources).map<Language>((value, index) => ({
      id: index,
      isoCode: value,
      isoCodeAlternativ: value,
    }));
  }

  async function getResources(params: ResourceParams | undefined) {
    if (params?.language) {
      return {
        [params.language]: staticResources[params.language],
      };
    }

    return staticResources;
  }

  return {
    getLanguages,
    getResources,
  };
}
