import React, { useCallback } from 'react';

import {
  GhostButton,
  Modal,
  SecondaryButton,
  useToggle,
} from '@daimler/react-ux-business-app';

import { ReactComponent as UserIcon } from '@icons/user.svg';

import { LocalizationDropdown } from '@components/LocalizationDropdown';

import { useTranslation } from '@daimler/react-ux-i10n';
import { VARIABLES } from '@configs/client/constants';
import { getUrl } from '@api/client/utils';

import {
  RightSectionWrapper,
  ModalFooterWrapper,
  StyledUserButton,
} from './styled';

export const RightSection = () => {
  const t = useTranslation();
  const { open, handleClose, handleOpen } = useToggle({
    defaultValue: false,
  });

  const handleLogOut = useCallback(() => {
    window.location.href = getUrl(VARIABLES.LogOutRedirectForWeb);
  }, []);

  return (
    <>
      <Modal
        onCancel={handleClose}
        open={open}
        title={t('Rabs.Controls.Login.LogOut')}
        footer={
          <ModalFooterWrapper>
            <GhostButton onClick={handleLogOut}>
              {t('Rabs.Common.Buttons.Ok')}
            </GhostButton>
            <SecondaryButton onClick={handleClose}>
              {t('Rabs.Common.Buttons.Cancel')}
            </SecondaryButton>
          </ModalFooterWrapper>
        }
      >
        {t('Rabs.Controls.Login.LogOutConfirmation')}
      </Modal>
      <RightSectionWrapper>
        <StyledUserButton icon={<UserIcon />} onClick={handleOpen} />
        <LocalizationDropdown />
      </RightSectionWrapper>
    </>
  );
};
