import { apiClient } from '@api/client';
import { combine, createEffect, createEvent, restore, sample } from 'effector';
import { useStore } from 'effector-react';
import { localStorageMandantIdKey } from './constants';

export const loadMandantsFx = createEffect(() => apiClient.mandants());
export const $mandants = restore(loadMandantsFx.doneData, null);

export const useMandants = () => useStore($mandants);

export const setCurrentMandantId = createEvent<string>(
  'SET_CURRENT_MANDANT_ID',
);

export const $currentMandantId = restore(
  setCurrentMandantId,
  localStorage.getItem(localStorageMandantIdKey) ?? '',
);

sample({
  clock: setCurrentMandantId,
  fn: (id) => {
    localStorage?.setItem(localStorageMandantIdKey, id);
  },
});

export const $currentMandant = combine(
  $mandants,
  $currentMandantId,
  (mands, id) => mands?.find((m) => m.id === +id),
);

export const useCurrentMandant = () => useStore($currentMandant);
export const useCurrentMandantId = () => useStore($currentMandantId);
