import {
  createStaticResourceProvider,
  createDynamicResourceProvider,
} from './providers';

import { setResources, setStaticResources } from './stores/resources';
import { setLanguages } from './stores/language';

import { getLocalizationEndpoint, flattenResources } from './utils';

import type { ResourcesThin } from './providers/types';
import type { LocalizationEndpointOptions } from './types';

export type initLocalizationParams = {
  staticResources: ResourcesThin;
  endpointUrl: string;
  getUrl?: (url: string) => string;
  endpointUrlOptions?: LocalizationEndpointOptions;
  useStatic?: boolean;
  env?: string;
};

export async function initLocalization(params: initLocalizationParams) {
  const {
    useStatic,
    staticResources,
    env,
    getUrl,
    endpointUrl,
    endpointUrlOptions,
  } = params;

  setStaticResources(staticResources);

  let provider;

  if (useStatic) {
    provider = createStaticResourceProvider(staticResources);
  } else {
    const url = await getLocalizationEndpoint({
      url: getUrl?.(endpointUrl) ?? endpointUrl,
      ...endpointUrlOptions,
    });

    provider = createDynamicResourceProvider(getUrl?.(url) ?? url);
  }

  const [languages, resources] = await Promise.all([
    provider.getLanguages(),
    provider.getResources({ environment: env }),
  ]);

  if (resources) {
    const flattenResourcesData = flattenResources(resources);

    setResources(flattenResourcesData);
  }

  if (!languages) return;

  setLanguages(languages.map((language) => language.isoCode));
}
