import type { VariablesType } from './types';

export const VARIABLES: VariablesType = {
  Version: 'unknown',
  ClientAppVersion: 'unknown',
  AppEnv: 'development',
  LogInRedirectForWeb: '/auth/login?ReturnUrl=%2F', // relative to baseHref
  LogOutRedirectForWeb: '/auth/sign-out', // relative to baseHref
  UseStaticResources: true,
};

declare let FRIA_RABS_WEBPACK_CONFIG:
  | { vars: Partial<VariablesType> }
  | undefined;

if (typeof FRIA_RABS_WEBPACK_CONFIG !== 'undefined') {
  Object.assign(VARIABLES, FRIA_RABS_WEBPACK_CONFIG.vars);
}
