import React, { memo, forwardRef } from 'react';

import { Select } from 'antd';

import { SelectAll } from './components/SelectAll';
import { Variant } from './constants';

import { SelectWrapper, StyledSelect, multiSelectCSS } from './styled';

import type { Option } from './types';

export type MultiSelectProps = {
  options: Option[];
  onSelect: (value: string[]) => void;
  onSelectAll: () => void;
  value: string[];
  placeholder: string;
  selectAllPlaceholder: string;
  variant?: Variant;
};

const MultiSelectComponent = forwardRef<HTMLDivElement, MultiSelectProps>(
  (
    {
      options,
      onSelect,
      onSelectAll,
      value,
      placeholder,
      selectAllPlaceholder,
      variant = Variant.Default,
    },
    ref,
  ) => {
    const isAllSelected = options.length === value.length;

    return (
      <SelectWrapper variant={variant} ref={ref}>
        <StyledSelect
          value={value}
          mode="multiple"
          maxTagCount={1}
          placeholder={placeholder}
          dropdownRender={(menu) => (
            <SelectAll
              onSelectAll={onSelectAll}
              selectAllPlaceholder={selectAllPlaceholder}
              isAllSelected={isAllSelected}
            >
              {menu}
            </SelectAll>
          )}
          onChange={(value) => onSelect(value as string[])}
        >
          {options.map(({ label, value }) => (
            <Select.Option key={value} value={value}>
              {label}
            </Select.Option>
          ))}
        </StyledSelect>
      </SelectWrapper>
    );
  },
);

export const MultiSelect = memo(MultiSelectComponent);

export { Variant, multiSelectCSS };
