import React, { memo, forwardRef } from 'react';

import { PickerProps } from 'antd/es/date-picker/generatePicker';
import type { Dayjs } from 'dayjs';

import AntdDatePicker from './utils';

import { DatePickerWrapper, datePickerCSS } from './styled';

type Ref = HTMLDivElement;
type DatePickerProps = {
  showTime?: boolean;
  format?: string;
} & Omit<PickerProps<Dayjs>, 'format' | 'showTime'>;

const defaultFormat = 'YYYY-MM-DD HH:mm';

const DatePickerComponent = forwardRef<Ref, DatePickerProps>(
  ({ showTime, format = defaultFormat, ...props }, ref) => (
    <DatePickerWrapper ref={ref}>
      <AntdDatePicker
        {...props}
        format={format}
        showTime={showTime ? { format: 'HH:mm' } : false}
      />
    </DatePickerWrapper>
  ),
);

export const DatePicker = memo(DatePickerComponent);

DatePicker.displayName = 'DatePicker';

export { datePickerCSS };
export type { DatePickerProps };
