import React, { forwardRef, memo } from 'react';

import { BaseButton, ButtonSize } from '../base';
import type { BaseButtonProps, ButtonRef, SizeType } from '../base';

import { GhostButtonWrapper } from './styled';

type GhostButtonProps = { size?: ButtonSize } & BaseButtonProps;

const GhostButtonComponent = forwardRef<ButtonRef, GhostButtonProps>(
  ({ size, disabled, ...restProps }, ref) => (
    <GhostButtonWrapper disabled={!!disabled}>
      <BaseButton
        {...restProps}
        type="ghost"
        ref={ref}
        disabled={disabled}
        size={size as SizeType}
      />
    </GhostButtonWrapper>
  ),
);

export const GhostButton = memo(GhostButtonComponent);

GhostButton.displayName = 'GhostButton';

export { ButtonSize };
export type { GhostButtonProps };
