import React, { forwardRef, memo } from 'react';

import { BaseButton, ButtonSize } from '../base';
import type { BaseButtonProps, ButtonRef, SizeType } from '../base';

import { SecondaryButtonWrapper } from './styled';

type SecondaryButtonProps = { size?: ButtonSize } & BaseButtonProps;

const SecondaryButtonComponent = forwardRef<ButtonRef, SecondaryButtonProps>(
  ({ size = ButtonSize.medium, disabled, ...restProps }, ref) => (
    <SecondaryButtonWrapper disabled={!!disabled}>
      <BaseButton
        {...restProps}
        type="default"
        ref={ref}
        disabled={disabled}
        size={size as SizeType}
      />
    </SecondaryButtonWrapper>
  ),
);

export const SecondaryButton = memo(SecondaryButtonComponent);

SecondaryButton.displayName = 'SecondaryButton';

export { ButtonSize };
export type { SecondaryButtonProps };
