import { HttpStatusCode } from '@daimler/react-ux-web-api';

export async function customFetch<T>(url: string) {
  try {
    const resp = await fetch(url);

    const { status } = resp;

    if (status === HttpStatusCode.OK) {
      return resp.text().then((text) => {
        if (text === '') return null;
        if (text.startsWith('<')) return null;

        return JSON.parse(text) as T;
      });
    }

    if (status !== HttpStatusCode.OK && status !== HttpStatusCode.CREATED) {
      throw new Error(`Error fething data`, {
        cause: resp,
      });
    }

    return null;
  } catch (error) {
    throw new Error(`Error fething data: ${error}`);
  }
}

export const objToQuery = (obj?: Record<string, string | number>) => {
  if (obj == null) return '';

  return `?${Object.entries(obj)
    .filter((p) => p[1] != null)
    .map((p) => `${encodeURIComponent(p[0])}=${encodeURIComponent(p[1])}`)
    .join('&')}`;
};
