import React, { useEffect, useState } from 'react';
import { AppFooterWrapper, AppFooterText } from './styled';
import { useTranslation } from '@daimler/react-ux-i10n';
import { apiClient } from '@api/client';

const webVersion = __WEB_VERSION__;
export const AppFooter: React.FC = () => {
  const t = useTranslation();
  const [serverVersion, setServer] = useState('unknown');
  const [env, setEnv] = useState('unknown');

  useEffect(() => {
    apiClient.systemConfiguration().then((config) => {
      if (config['Version']) {
        setServer(config['Version']);
      }

      if (config['AppEnv']) {
        setEnv(config['AppEnv']);
      }
    });
  }, []);

  return (
    <AppFooterWrapper>
      <AppFooterText>{`${t(
        'Rabs.Footer.Copyright',
      )} ${serverVersion}(WS)-${webVersion}(Web)-${env}`}</AppFooterText>
    </AppFooterWrapper>
  );
};

