import { Permission } from '@permissions/constants';
import type { Nullable } from 'global-types';

export enum Path {
  DASHBOARD = '/',
  INVOICES = '/invoices',
  INVOICE = '/invoices/:id',
  INVOICEWITHFILTER = '/invoices/:id/:filter',
  MANDANTS = '/mandants',
  GOODSMOVEMENTS = '/goods-movements',
  GOODSMOVEMENT = '/goods-movements/:id',
  GOODSMOVEMENTWITHFILTER = '/goods-movements/:id/:filter',
}

export enum TileGroup {
  INVOICES = 'Invoices',
}

export type RouteType = {
  path: Path;
  element: React.ReactNode;
  title: React.ReactNode | string;
  description: React.ReactNode | Nullable<string>;
  tileGroup: TileGroup | undefined;
  permissions: Permission[] | undefined;
};

export type RoutesType = Array<RouteType>;
