export type CustomFetchOptions = Partial<{
  beforeRequest: (init?: RequestInit) => RequestInit;
  afterRequest: (response: Response) => Promise<Response>;
}>;

export function createCustomFetch(options?: CustomFetchOptions) {
  const { afterRequest, beforeRequest } = options ?? {};

  const customFetch = async (input: RequestInfo | URL, init?: RequestInit) => {
    const fetchInit = beforeRequest?.(init) ?? init;
    const resp = await fetch(input, fetchInit);

    return afterRequest?.(resp) ?? resp;
  };

  return customFetch;
}
