import React, { memo, forwardRef } from 'react';

import { Dropdown as AntdDropdown } from 'antd';

import { DropdownContainer, StyledMenu, StyledGhostButton } from './styled';

type DropdownProps = {
  onSelect: (value: string) => void;
  options: string[];
  value: string;
};

const DropdownComponent = forwardRef<HTMLDivElement, DropdownProps>(
  ({ value, onSelect, options }, ref) => {
    const handleSelect = (value: { key: string }) => {
      onSelect(value.key as string);
    };

    return (
      <DropdownContainer ref={ref}>
        <AntdDropdown
          trigger={['click']}
          overlayClassName="dropdown"
          overlay={
            <StyledMenu
              onSelect={handleSelect}
              selectable
              items={options.map((option) => ({
                key: option,
                label: option,
              }))}
            />
          }
          placement="bottom"
          arrow
        >
          <StyledGhostButton>{value}</StyledGhostButton>
        </AntdDropdown>
      </DropdownContainer>
    );
  },
);

export const Dropdown = memo(DropdownComponent);

Dropdown.displayName = 'Dropdown';

export type { DropdownProps };
