import { loadMandantsFx, useCurrentMandantId } from '@mandants/index';
import React, { useEffect } from 'react';
import { Suspense } from 'react';
import { Routes as RootRoutes, Route, useNavigate } from 'react-router-dom';

import NotFoundPage from './404';
import { routes } from './routes';
import { setPermissions } from '@permissions/index';
import { apiClient } from '@api/client/index';
import { Path } from '@router/types';

export const Pages = () => {
  useEffect(() => {
    loadMandantsFx();
  }, []);

  const navigate = useNavigate();
  const currentMandantId = useCurrentMandantId();

  useEffect(() => {
    if (!currentMandantId) {
      navigate(Path.MANDANTS);
    }
    apiClient.authState().then((s) => setPermissions(s.permissions ?? []));
  }, [currentMandantId]);

  return (
    <Suspense fallback={null}>
      <RootRoutes>
        {routes.map(({ path, element }) => (
          <Route path={path} element={element} key={path} />
        ))}
        <Route path="*" element={<NotFoundPage />} />
      </RootRoutes>
    </Suspense>
  );
};
